:root {
  --background: #eafdfc;
  --primary: #06283d;
  --accent1: #91d8e4;
  --accent2: #bfeaf5;
}

html, body {
  background-color: var(--background);
  color: var(--primary);
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.main {
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  display: flex;
}

.main-heading {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

.form {
  justify-content: center;
  display: flex;
}

input {
  border: 2px solid var(--accent2);
  background: url("enter.99157987.png") right 10px center / 20px no-repeat;
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px;
  font-weight: bold;
}

input:focus {
  border-color: var(--accent1);
  outline: none;
}

.invalid {
  border-color: red;
}

button {
  background-color: var(--primary);
  color: var(--background);
  cursor: pointer;
  border: 2px solid #0000;
  border-radius: 5px;
  padding: 10px;
  font-weight: bold;
  transition: all .1s ease-in-out;
}

button:hover {
  background-color: var(--accent2);
  border-color: var(--primary);
  color: var(--primary);
}

.weather {
  justify-content: center;
  align-items: end;
  margin-top: 30px;
  margin-bottom: 10vh;
  display: flex;
}

.weather-details {
  flex-direction: column;
  align-items: start;
  display: flex;
}

.weather-temp {
  margin-right: 30px;
  font-size: 3rem;
  font-weight: bold;
  line-height: 75%;
}

.weather-contition {
  letter-spacing: 3px;
}

.weather-location {
  font-size: 1.2rem;
  font-weight: bold;
}

hr {
  width: 100%;
  border: 1px solid var(--accent2);
  margin: 30px 0 0;
}

